* {
  margin: 0;
  padding: 0;
}

.html {
  box-sizing: border-box;
}

ul {
  list-style: none;
}

.wrapper {
  max-width: 100%;
  background-color: #141414;
  margin: 0 auto;
}

.header-container, .nav-item {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.header-container, .hero-container {
  max-width: 100%;
  padding-left: 8%;
  padding-right: 8%;
}

.navbar {
  letter-spacing: -.03em;
  color: #fff;
  align-items: center;
  font-family: Epilogue;
  font-size: 15px;
  font-style: normal;
  font-weight: 400;
  line-height: 15px;
  text-decoration: none;
  display: flex;
}

.logo a {
  color: #fff;
  letter-spacing: -.03em;
  font-family: Epilogue, sans-serif;
  font-size: 15px;
  font-style: normal;
  font-weight: 400;
  line-height: 15px;
}

.nav-item {
  gap: 40px;
  margin-left: 80px;
  margin-right: auto;
  display: flex;
}

.nav-item li a {
  text-align: center;
  text-transform: uppercase;
  color: #fff;
  font-family: Epilogue, sans-serif;
  font-size: 9px;
  font-style: normal;
  font-weight: 400;
  line-height: 12px;
  text-decoration: none;
}

.nav-phone {
  cursor: pointer;
  background: #1e2827;
  border-radius: 8px;
  align-items: center;
  padding: 15px 35px;
  display: flex;
}

.nav-phone a {
  color: #fff;
  text-align: center;
  font-family: Epilogue;
  font-size: 9px;
  font-style: normal;
  font-weight: 400;
  line-height: 12px;
  text-decoration: none;
}

.hero-content {
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  padding-top: 98px;
  display: flex;
}

.hero-section-title {
  letter-spacing: -.04em;
  color: #fff;
  font-family: Epilogue, sans-serif;
  font-size: 60px;
  font-style: normal;
  font-weight: 600;
  line-height: 62px;
}

.hero-section-text {
  letter-spacing: -.02em;
  color: #fff;
  flex-wrap: wrap;
  font-family: Epilogue, sans-serif;
  font-size: 14px;
  font-style: normal;
  font-weight: 300;
  line-height: 18px;
  display: flex;
}

.hero-images {
  justify-content: space-between;
  align-items: center;
  gap: 60px;
  padding-top: 80px;
  padding-bottom: 80px;
  display: flex;
}

.watch-slides {
  text-align: center;
  max-width: 435px;
  max-height: 255px;
  background: #1e2827;
  padding: 2rem;
}

.hero-text-mobile {
  letter-spacing: -.02em;
  color: #fff;
  font-family: Epilogue, sans-serif;
  font-size: 14px;
  font-style: normal;
  font-weight: 300;
  line-height: 18px;
}

.watch-slides img {
  object-fit: cover;
  width: 100%;
}

.hero-button {
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding-top: 35px;
  display: flex;
}

.pre-btn, .nxt-btn {
  width: 40px;
  height: 40px;
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border: 1px solid rgba(255, 255, 255, .5);
  border-radius: 4px;
  margin-left: 12px;
}

.pre-btn:hover, .pre-btn:focus, .nxt-btn:hover, .nxt-btn:focus {
  border: 2px solid #fff;
}

.arrow {
  width: 18px;
  height: 18px;
}

.slide-number {
  letter-spacing: -.02em;
  color: #fff;
  flex-direction: row;
  justify-content: space-between;
  font-family: Epilogue, sans-serif;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 18px;
  display: flex;
}

.slide-number span:before {
  content: "";
  width: 40px;
  height: 1px;
  vertical-align: middle;
  background-color: #fff;
  margin-left: 10px;
  margin-right: 10px;
  display: inline-block;
}

.hero-text-mobile {
  display: none;
}

.js-menu-container {
  color: #fff;
  width: 100%;
  width: 100%;
  height: 100%;
  z-index: 1000;
  background-color: #1e2827;
  flex-direction: column;
  align-items: center;
  padding: 20px;
  display: none;
  position: fixed;
  top: 0;
  right: 0;
}

.button--toggle-menu svg {
  color: #fff;
  width: 40px;
  height: 40px;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  display: block;
  position: absolute;
  top: 35px;
  right: 10px;
}

.mobile-navigation {
  flex-direction: column;
  gap: 100px;
  padding-top: 25px;
  display: flex;
}

.navbar-mobile {
  justify-content: space-between;
  margin-bottom: 50px;
  display: flex;
}

.js-menu-container .nav-item {
  flex-direction: column;
  padding-top: 200px;
  display: flex;
}

.js-menu-container .nav-container li a {
  color: #fff;
  letter-spacing: .02em;
  font-family: Epilogue, sans-serif;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 18px;
}

.is-open {
  display: block;
}

.js-open-menu {
  cursor: pointer;
  display: none;
}

.js-close-menu {
  color: #fff;
  width: 40px;
  height: 40px;
  z-index: 1;
  cursor: pointer;
  background-color: #1e2827;
  border: none;
  flex-shrink: 0;
  font-size: 44px;
  position: absolute;
  top: 25px;
  right: 20px;
}

@media screen and (max-width: 375px) {
  .wrapper {
    width: 100%;
    margin: 0 auto;
  }

  .header-container, .hero-container {
    margin: 0 auto;
    padding-left: 0;
  }

  .js-open-menu {
    display: block;
  }

  .logo {
    margin-top: 20px;
  }

  .nav-item, .nav-phone, .hero-section-title br {
    display: none;
  }

  .hero-section-title {
    align-items: center;
    padding: 65px 0;
    font-size: 30px;
    line-height: 32px;
  }

  .hero-section-text {
    display: none;
  }

  .hero-content {
    flex-direction: column;
    gap: 10px;
    padding-top: 0;
  }

  .hero-images {
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    padding-top: 0;
    display: flex;
  }

  .watch-slides {
    max-width: 266px;
    max-height: 200px;
    margin-bottom: 20px;
  }

  .control-button {
    flex-direction: row;
    gap: 12px;
    display: flex;
  }

  .hero-button {
    margin-top: 50px;
    margin-bottom: 40px;
  }

  .pre-btn {
    margin-left: 0;
  }

  .hero-text-mobile {
    justify-content: end;
    display: flex;
  }

  .hero-text-mobile br {
    display: none;
  }

  .slide-number {
    flex-wrap: nowrap;
    gap: 5px;
  }

  .hero-watch {
    width: 100%;
    margin-top: 50px;
    padding-top: 110px;
  }
}

@media screen and (min-width: 376px) and (max-width: 768px) {
  .header-container, .hero-container {
    flex-wrap: wrap;
    padding-left: 0;
    padding-right: 0;
  }

  .nav-item, .nav-phone {
    justify-content: space-between;
    margin-left: 40px;
    display: none;
  }

  .hero-images {
    display: block;
  }

  .watch-slides {
    margin-bottom: 100px;
  }

  .hero-text-mobile {
    display: none;
  }

  .js-open-menu {
    display: block;
  }

  .hero-watch {
    max-width: 100%;
    height: auto;
    display: block;
  }
}

@media screen and (min-width: 769px) and (max-width: 1280px) {
  .wrapper {
    max-width: 1440px;
  }

  .nav-item, .nav-phone {
    justify-content: space-between;
    display: flex;
  }

  .watch-slides {
    align-items: center;
    padding-top: 65px;
  }

  .hero-button {
    flex-direction: row;
    align-items: center;
    padding-top: 65px;
    display: flex;
  }

  .control-button {
    flex-wrap: nowrap;
    display: flex;
  }

  .js-open-menu {
    display: none;
  }

  .hero-watch {
    max-width: 60%;
    height: auto;
    display: block;
  }
}

